export default {
	name: 'vue-pwd',
	props: {
		dispay: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			password: [] // 存储密码的数组           
		}
	},
	methods: {
		// 虚拟键盘按键事件
		keysOn(d) {
			console.log(d)
			if (d!= 'del') {
				if (this.password.length < 6 && d) {
					this.password.push(d);
					if (this.password.length == 6) {
						this.$emit('getData', {
							pwd: this.password
						}) // 当位数满6个时，触发父组件定义的事件，父组件定义事件的方法（v-on:getData="methods方法"）
					}
				}
			} else {
				this.password.pop();
			}
		},
		// 密码输入完成后，供父组件调用的方法（当密码正确时，关闭密码框，错误时弹出提示，并可重新输入）
		// 此方法在父组件调用方法为 this.$refs.payPassword.tips(false); 参数为布尔值。
		tips(bln) {
			if (bln) {
				this.dispay = false;
				this.password = [];
			} else {
				this.dispay = true;
				this.password = [];
				if (!alert('密码错误，请重新输入！')) {
					this.dispay = false;
				}
			}
		},
		// 手动打开关闭密码框
		open(bln) {
			if (bln) this.dispay = true;
			else this.dispay = false;
		},
		hide() {
			this.dispay = false;
		},
		goto() {
			this.$emit('link', {
				pwd: this.password
			});
		}
	}
}
